import apiClient from '../../api-client';
import { CreateUserType } from "../../../services/redux/reducers/update-user/update-user.slice";
import { BasicApiResponse } from '../../../typings/api';

const GET_ME = '/v1/users/details';
const UPDATE_USER = (userId: number) => `/v1/users/update-user/${userId}`;

const UserService = {
    getMe: async (): Promise<BasicApiResponse<any>> => apiClient(GET_ME, undefined, undefined, 'GET'),
    updateUser: async (userId: number, createUserForm: CreateUserType) => apiClient(UPDATE_USER(userId), createUserForm, undefined, 'PUT'),
};

export default UserService;
