import React, {useEffect, useState} from 'react';
import {faAngleRight} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {Typeahead} from "react-bootstrap-typeahead";
import 'react-bootstrap-typeahead/css/Typeahead.css';
import {useNavigate} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../services/redux/root-reducers";
import {AuthState, createUser, logIn} from "../../services/redux/reducers/auth/auth.slice";
import Input from "../../ui-components/input/input";

type RegisterFormProps = {
    step: number
    setStep: Function
    setRegisterForm: Function
};

function RegisterForm({step, setStep, setRegisterForm}: RegisterFormProps) {
    const [firstname, setFirstName] = useState("");
    const [lastname, setLastName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [checkPassword, setCheckPassword] = useState("");
    const [phone, setPhone] = useState(0);
    const [fixPhone, setFixPhone] = useState(0);
    const [linkedinUrl, setLinkedInUrl] = useState("");
    const [skills, setSkills] = useState("");


    const [firstnameError, setFirstnameError] = useState(false);
    const [lastnameError, setLastnameError] = useState(false);
    const [emailError, setEmailError] = useState(false);
    const [pwdError, setPwdError] = useState(false);
    const [pwdCheckError, setPwdCheckError] = useState(false);
    const [phoneError, setPhoneError] = useState(false);
    const [phoneFixedError, setPhoneFixedError ]= useState(false);
    const [linkedinError, setLinkedinError] = useState(false);

    const [firstnameErrorMsg, setFirstnameErrorMsg] = useState("Le prnom est obligatoire");
    const [lastnameErrorMsg, setLastnameErrorMsg] = useState("Le nom est obligatoire");
    const [emailErrorMsg, setEmailErrorMsg] = useState("L'email n'est pas valide");
    const [pwdErrorMsg, setPwdErrorMsg] = useState("Minimum 8 caractres et un caractres spcial");
    const [pwdCheckErrorMsg, setPwdCheckErrorMsg] = useState("Les mot de passes ne sont pas similaires");
    const [phoneErrorMsg, setPhoneErrorMsg] = useState("Le numro de tlphone n'est pas valide");
    const [phoneFixedErrorMsg, setPhoneFixedErrorMsg ]= useState("Le numro de tlphone fixe n'est pas valide");
    const [linkedinErrorMsg, setLinkedinErrorMsg] = useState("Le lien n'est pas un lien linkedin");

    const [form, setForm] = useState({
        firstname: '',
        lastname: '',
        email: '',
        password: '',
        checkPassword: '',
        phone: 0,
        userType: 'pro'
    });

    const checkForm = () => {
        let email_regex = new RegExp('[a-z0-9]+@[a-z]+\.[a-z]{2,3}');
        let pwd_regex = new RegExp('(?=.{8,})(?=.*[^A-Za-z0-9])')
        if (!email_regex.test(email) || !pwd_regex.test(password) || password != checkPassword || firstname.length < 1 || lastname.length < 1) {
            if (!email_regex.test(email)) {
                setEmailError(true);
            }
            if (!pwd_regex.test(password)) {
                setPwdError(true);
            }
            if (password != checkPassword)
                setPwdCheckError(true);
            if (firstname.length < 1)
                setFirstnameError(true);
            if (lastname.length < 1)
                setLastnameError(true);
            return false;
        }
        return true;
    }

    useEffect(() => {
        setFirstnameError(false);
        setLastnameError(false);
        setEmailError(false);
        setPwdError(false);
        setPwdCheckError(false);
    }, [email, password, checkPassword, firstname, lastname])

    const onSubmit = () => {
        if (checkForm()) {
            setStep(2);
        }
    }

    const handleChange = (e: any) => {
        setForm({...form,[e.target.id]: e.target.value });
        setRegisterForm(form);
    };


    useEffect(() => {
        setForm({...form,["phone"]: phone})
        setRegisterForm(form)
    }, [phone])


    const returnRegisterForm = (step: number) => {
        if (step === 2) {
            return (
                <div className={"sign_form-container"}>
                    <Input type={"phone"} id={"phone"} title={"Tlphone Mobile"} doThings={setPhone} hasError={phoneError} errorMsg={phoneErrorMsg} createForm={handleChange}/>
                    <Input type={"phone"} id={"phoneFixed"} title={"Tlphone Fix"} doThings={setPhone} hasError={phoneFixedError} errorMsg={phoneFixedErrorMsg} createForm={handleChange}/>
                    <Input type={"text"} id={"portfolioUrl"} title={"Lien LinkedIn"} doThings={setLinkedInUrl} hasError={linkedinError} errorMsg={linkedinErrorMsg} createForm={handleChange}/>
                </div>
            )
        } else if (step === 3) {
            return (
                <form id="register" className={"sign_form-container"}>
                    <Typeahead id="rome" placeholder="Code Rome" className={"sign_input-typeahead"}
                               multiple={true}
                               options={['1', '2', '3']}
                    />
                    <Input type={"text"} title={"Comptences"} doThings={setSkills} />
                </form>
            )
        } else {
            return (
                <form id="register" className={"sign_form-container"}>
                    <Input type={"text"} id={"firstname"} title={"Prnom"} doThings={setFirstName} hasError={firstnameError} errorMsg={firstnameErrorMsg} createForm={handleChange} />
                    <Input type={"text"} id={"lastname"} title={"Nom"} doThings={setLastName} hasError={lastnameError} errorMsg={lastnameErrorMsg} createForm={handleChange} />
                    <Input type={"text"} id={"email"} title={"Email"} doThings={setEmail} hasError={emailError} errorMsg={emailErrorMsg} createForm={handleChange} />
                    <Input type={"password"} id={"password"} title={"Mot de passe"} doThings={setPassword} hasError={pwdError} errorMsg={pwdErrorMsg} createForm={handleChange} />
                    <Input type={"password"} id={"checkPassword"} title={"Confirmation"} doThings={setCheckPassword} hasError={pwdCheckError} errorMsg={pwdCheckErrorMsg} createForm={handleChange}/>
                    <Input type={"submit"} title={"Continuer"} doThings={onSubmit} iconName={faAngleRight}/>
                </form>
            )
        }
    }

    return (
        <>
        {returnRegisterForm(step)}
        </>
    );
}

export default RegisterForm;