export const isDevConfig = () => process.env.REACT_APP_ENV === "DEV";

export const isRecConfig = () => process.env.REACT_APP_ENV === "REC";

export const isProdConfig = () => process.env.REACT_APP_ENV === "PROD";

export const getConfig = () => {
    const devConfig = {
        environment: "dev",
        urlHostApi: "http://localhost:8080/api",
    };

    const recConfig = {
        environment: "rec",
        urlHostApi: "http://startbuilding-dev.app-joza-it.fr/api",
    };

    const prodConfig = {
        environment: "prod",
        urlHostApi: "http://startbuilding-rec.app-joza-it.fr/api",
    };

    if (isRecConfig()) {
        return recConfig;
    } else if (isProdConfig()) {
        return prodConfig;
    } else {
        return devConfig;
    }
};

export default getConfig();
