import React from 'react';
import "./header.scss"
import home from './../../asset/svg/home.svg'
import fake from './../../asset/svg/header.svg'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faBars} from "@fortawesome/free-solid-svg-icons";
import {useNavigate} from "react-router-dom";
import {useSelector} from "react-redux";
import {RootState} from "../../services/redux/root-reducers";
import {UserState} from "../../services/redux/reducers/user/user-reducer";
import dashboard from "../../asset/svg/dashboard.svg";
import CustomButton from "../button/custom-button";


const Header = () => {
    const navigate = useNavigate();
    const {response} = useSelector<RootState, UserState>((state) => state.currentUser);
    return (
        <div className={"header-main"}>
            <img className={"logo"} alt={""} src={home}/>
            <div>
                <h6>Bienvenue {response.content?.firstName}</h6>
                <h4>Votre tableau de bord</h4>
                <div className={"header-btn"}>
                    <CustomButton title={"Dashboard"} icon={dashboard} selected/>
                    <CustomButton title={"Actualit"} icon={dashboard}/>
                </div>
            </div>
            <div className={"header-right"}>
                <div className={"disconnect-container"}>
                    <FontAwesomeIcon icon={faBars} onClick={() => {
                        localStorage.removeItem('access_token');
                        navigate('/login')
                    }}/>
                </div>
            </div>
            {/*<img src={fake} className={"fakeheader"}/>*/}
        </div>
    );
};

export default Header