import React from 'react';
import './input.scss';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faAngleRight, IconDefinition} from "@fortawesome/free-solid-svg-icons";

type InputProps = {
    type: string
    title: string
    iconName?: IconDefinition
    doThings: Function
    hasError?: boolean
    errorMsg?: string
    createForm?: Function
    id?: string
}

const Input = ({type, title, iconName, doThings, hasError, errorMsg, createForm, id} : InputProps) => {
    const returnByInputType = () => {
        if (type === 'text' || type === "password") {
            return (
                <>
                    <div className={"sign_input-container"}>
                        { hasError && <span className={"required"}>*</span> }
                        <input id={id} type={type === 'text'? "text":"password"} required={true} onChange={(e) => {
                            doThings(e.target.value)
                            { createForm && createForm(e)}
                        }}/>
                        <span>{title}</span>
                        { hasError && <p className={"errorMsg"}>{errorMsg}</p>}
                    </div>
                </>
            )
        } else if (type === 'phone') {
            return (
                <>
                    <div className={"sign_input-container"}>
                        { hasError && <span className={"required"}>*</span> }
                        <input id={id} type={"tel"} required={true} onChange={(e) => {
                            { createForm && createForm(e)}
                            doThings(e.target.value)
                        }}/>
                        <span>{title}</span>
                    </div>
                    { hasError && <span className={"errorMsg"}>{errorMsg}</span>}
                </>
            )
        } else if (type === 'submit' && iconName) {
            return (
                <div className={"sign_input-container"}>
                    <input value={title} className={"submit"} onClick={() => doThings()}/>
                    <FontAwesomeIcon icon={iconName} className={"icon"}/>
                </div>
            )
        }
    }


    return (
        <>
            {returnByInputType()}
        </>
    );
};

export default Input;
