import React from 'react';
import './custom-button.scss'

type CustomButtonProps = {
    title: string
    icon: string
    selected?: boolean
}
const CustomButton = ({title, icon, selected}: CustomButtonProps) => {
    return (

        <span className={selected && selected?"custom-button selected":"custom-button"}>
            <img src={icon} alt={""}/>
            <span>{title}</span>
        </span>
    );
};

export default CustomButton;
