import React from 'react';
import './custom-button.scss'

type NavbarButtonProps = {
    icon: string
    selected?: boolean
    doThings?: Function
}

const NavBarButton = ({icon, selected, doThings}: NavbarButtonProps) => {
    return (
        <div className={selected && selected?"custom-icon selected":"custom-icon"} onClick={() => doThings} >
            <img src={icon}/>
        </div>
    );
};

export default NavBarButton;
