import {AuthSigninForm, CreateUserForm, ResetPasswordForm, User} from '../../../typings/api';
import apiClient from '../../api-client';
const LOGIN = '/v1/public/auth/sign-in';
const CREATE_PASSWORD = '/v1/public/users/register';
const FORGOT_PASSWORD = '/v1/auth/public/forgot-password';

const AuthService = {
    loginUser: async (form: AuthSigninForm) => apiClient(LOGIN, form, undefined, 'POST'),
    createUser: async (form: CreateUserForm) => apiClient(CREATE_PASSWORD, form, undefined, 'POST'),
};

export default AuthService;
